/* -------- VAR -------- */
:root {
    --rowHeight: 20px;
    --timelineHeight: 60px;

    --timelineWidth: 1000px;

    //the horizontal portion of the container that should be 
    //taken up by the gantt chart
    --chartPortionPct: 75%; //for flex
    --chartPortionFr: 75fr; //for grid

    --tbh-blue-background: rgb(35, 45, 64);
    --tbh-blue1: rgb(25, 128, 143);
    --tbh-blue2: rgb(9, 52, 63);
}

/*begin css reset*/

body {
    line-height: 1;
    font-family: Helvetica, Arial, sans-serif;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*end css reset*/

body {
    margin: 0px;
    // font-family: 'Segoe UI Semibold',wf_segoe-ui_semibold,helvetica,arial,sans-serif;
}

h4 {
    font-size: larger;
    font-weight: 500;
    margin: 0px;

    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

/* -------- DIV -------- */

div {
    padding: 0px;
    margin: 0px;

    //border: 1px dashed black;
}

//div for the header of the graph: title, logo etc.
#div-header {
    min-height: 50px;

    border-style: none none solid none;
    border-width: 1px;
    border-color: black;

    background: var(--tbh-blue-background);
    color: white;

    position: relative;
    padding: 10px;
}

//div that handles the overlapping of the content and status line. Grid.
#div-content {
    height: fit-content;
}


//the div below the above div that contains the activitys table and gantt
#div-content {
    height: 100%;
    width:100%;
    display: flex;
}

//div that contains the status line and accompanying text.
//Overlaps gantt and timeline but is one level up in the DOM heirachy,
#div-statusLine {
    height: 100%;
    z-index: 1;

    pointer-events: none;
}

#div-chart {
    width: var(--chartPortionPct);
    border-style: none none none solid;
    border-width: 2px;
    border-color: black;

    overflow: scroll;
}

//div that contains the activities table
#div-activities {
    width: 25%;
    flex-shrink: 0;
    overflow-x: scroll;
    overflow-y: scroll;
    height: 100%;
}

#div-chart::-webkit-scrollbar {
    height: 20px;
}

#div-timeline {
    height: 100%;

    border-bottom: 1px gray;
    width: 75%;
    overflow-x: scroll;
    overflow-y: clip;
}

#div-timeline::-webkit-scrollbar {
    height: 0px;
}


/* ------- TABLE ------- */

table {
    width: 100%;
    border: 1px solid blue;
}

#table-activityHeader {
    width: 25%;
    color: #bbbbbb;
    flex-shrink: 0
}

th {
    height: var(--timelineHeight);
    ;
}

tr {
    height: var(--rowHeight);
}

td {
    min-width: 40px;

    border: 1px #aaaaaa;
    border-style: solid none solid solid;
}

/* #table-ganttGrid{

} */

/* ------- TIMELINE ------- */

//check these
#svg-statusLine {
    z-index: 3;
}

#statusLine {
    stroke: rgb(0, 255, 255);
    stroke-width: 2px;
}

.g-tl {
    border: 1px solid black;
    //color:#00ff00;
}

.yearText {
    font-size: 26px;
    //height: 1em;
}

.monthText {
    text-anchor: middle;
}

/* ------- CHART ------- */

.activityBar {

    fill: red;
    stroke: black;
    stroke-width: 1px;
}


::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    border: none;
}

::-webkit-scrollbar-track {
    background: var(--tbh-blue-background);
}

::-webkit-scrollbar-corner {
    background: var(--tbh-blue-background);
    
}

::-webkit-scrollbar-thumb {
    background: var(--tbh-blue1);

}

::-webkit-scrollbar-thumb:hover {
    background: rgb(200, 200, 200);
}

/* ------- DEBUG ------- */

.highlight {
    background-color: rgba(0, 255, 0, 0.25);
    border: 1px solid #00ff00;
}

.style1 {
    padding-left: 0px;
}

.style2 {
    padding-left: 5px;
}